
@import url('https://fonts.googleapis.com/css?family=Muli&display=swap');

/*nav bar*/
*{

  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: muli;
}
.navbar{
  background-color: #3498db !important;
  
}
.navbar-brand{
  font-size: 1.9rem !important;
  
}
.brand-name{
  color:#3498db !important;
}
.navbar a{
  color:#3498db !important;
}
@media(max-width: 991px){
  .navbar-brand{
    font-size: 1.5rem !important;
  }
  .menu_active{
    font-weight: bold;
    border-bottom:none;
  }
}

/*main content*/
#header{
width:100%;
height :80vh;
margin-top:70px;
flex-wrap: wrap;

}
#header h1{
  margin: 0 0 10px 0;
  font-size: 35px;
  font-weight: 700;
  color:black;
}
#header h4{
  color: black;
  font-size: 24px;
}

#header .btn-get-started{
  font-weight: 500;
  font-size:16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: #3498db;
  border: 2px solid #3498db;
  text-decoration: none;
}

#header .btn-get-started:hover{
  background: #3498db;
color: #ffffff;
}

.header-img{
  max-width:100%;
  height: auto;
}

.col-6{
  padding-top: 50px;
}

.jumbotron{
  background: gainsboro;
  
}


